<template>
    <div id="root">
        <v-app style="text-align: center">
            <div>
                <v-btn fixed fab bottom right
                       color="#597da3"
                       @click="getAllPhotos"
                       style="opacity: 0.9;margin-bottom: 40px">
                    <v-icon color="white">mdi-refresh</v-icon>
                </v-btn>

                <input ref="fileInput"
                       type="file"
                       style="display: none"
                       accept="image/*"
                       @change="onFileChange">


                <v-bottom-navigation fixed height="50" grow color="#9E9E9E">
                    <v-btn v-on:click="SetActivePanel('Main')">
                        <span :style="activePanel === 'Main' ? 'color: red': ''">Лента</span>
                        <v-icon :color="activePanel === 'Main' ? ' red': ''">mdi-image</v-icon>
                    </v-btn>

                    <v-btn v-on:click="SetActivePanel('UserProfile')">
                        <span :style="activePanel === 'UserProfile' ? 'color: red': ''">Аккаунт</span>
                        <v-icon :color="activePanel === 'UserProfile' ? ' red': ''">mdi-account</v-icon>
                    </v-btn>

                    <v-btn>
                        <v-icon size="40">mdi-arrow-up-drop-circle-outline</v-icon>
                    </v-btn>

                    <v-btn v-on:click="SetActivePanel('UserSettings')">
                        <span :style="activePanel === 'UserSettings' ? 'color: red': ''">Настройки</span>
                        <v-icon :color="activePanel === 'UserSettings' ? ' red': ''">fa-cog</v-icon>
                    </v-btn>

                    <v-btn v-on:click="SetActivePanel('UserSettings2')" v-if="zis">
                        <span :style="activePanel === 'UserSettings2' ? 'color: red': ''">Настройки</span>
                        <v-icon :color="activePanel === 'UserSettings2' ? ' red': ''">fa-cogs</v-icon>
                    </v-btn>
                </v-bottom-navigation>

            </div>
            <VKView id="view" :activePanel="activePanel">
                <VKPanel id="Main">
                    <v-k-panel-header>НЕДОлента</v-k-panel-header>
                    <div class="bg-light" style="display:inline-flex; flex-wrap: wrap;margin-bottom: 50px">
                        <v-col v-for="(photo, index) in photos" :key="index">
                            <v-card style="border-radius: 25px;padding-bottom: 5px; padding-top: 5px" max-width="150" class="center-pic">
                                <v-img
                                        @mouseover="setModalInfo(photo.url, photo.text)"
                                        @click="dialog = true"
                                        class="center-pic"
                                        width="150px"
                                        height="150px"
                                        :src="photos.length > 1 ? getImgUrl(photo.url) : photo.url"
                                        style="border-radius: 25px"/>
                            </v-card>
                            <div style="justify-content: center; border-radius: 25px;padding-top: 2px">
                                <v-btn rounded
                                       style="font-size: 1rem; right: 2px"
                                       @click="isNull(photo.like) ? setLikeOnPhoto_null(photo, index) : null">
                                    <v-icon :color="isNull(photo.like) ? 'grey': photo.like ? 'pink' : 'grey'">mdi-heart</v-icon>
                                    {{abbreviateNumber(photo.count_like)}}
                                </v-btn>
                                <v-btn rounded
                                       style="font-size: 1rem; left: 2px"
                                       @click="isNull(photo.like) ? setDisOnPhoto_null(photo, index) : null">
                                    <v-icon :color="isNull(photo.like) ? 'grey': photo.like ? 'grey' : '#4FC3F7'">mdi-heart-broken</v-icon>
                                    {{abbreviateNumber(photo.count_dis)}}
                                </v-btn>
                            </div>
                        </v-col>
                    </div>
                </VKPanel>

                <VKPanel id="UserProfile">
                    <v-k-panel-header>Аккаунт</v-k-panel-header>
                    <v-card :color="this.$vuetify.theme.dark ? '#525252' : 'rgb(242,242,242)'">
                        <v-container>
                            <v-row>
                                <v-col cols="auto">
                                    <v-img :src="this.userAvatar" width="150" height="150" style="border-radius: 100%"/>
                                </v-col>
                                <v-col cols="auto">
                                    <p style="font-size: 2rem">{{this.name}}</p>
                                    <p style="opacity: 0.54;">АРАБ ПРИВЕТ</p>
                                </v-col>
                            </v-row>
                        </v-container>
                        <v-card>
                            <v-subheader>Ваши посты:</v-subheader>
                            <p style="opacity: 0.5;padding-bottom: 10px">У Вас нет постов.</p>
                        </v-card>
                        <v-card class="profileCard">
                            <v-subheader>Достижения:</v-subheader>
                            <h5 style="opacity: 0.54;">В разработке</h5>
                            <v-img src="https://vk.com/sticker/1-15597-512"
                                   width="150" height="150"
                                   class="center-pic"/>
                        </v-card>
                    </v-card>
                </VKPanel>

                <VKPanel id="UserSettings">
                    <v-k-panel-header>Настройки</v-k-panel-header>
                    <v-card>
                        <v-container>
                            <v-subheader class="pl-0">Количество загружаемых фотографий: {{count}}</v-subheader>
                            <v-slider
                                    v-model="count"
                                    min="10"
                                    max="35"
                            />
                            <v-switch v-model="getRandomPhotos" label="Случайные фотографии"/>
                            <v-switch @change="SetNightMode" :value="this.$vuetify.theme.dark" label="Ночноый режим"
                                      append-icon="fa-moon-o"/>
                            <v-switch label="Фича" @change="SetNightMode"/>
                            <v-spacer>
                                <v-img src="https://vk.com/sticker/1-15597-128" width="150" height="150"
                                       class="center-pic"/>
                                <v-btn style="border-radius: 10px" color="#c0c0c0">Сохранить</v-btn>
                            </v-spacer>
                        </v-container>
                    </v-card>
                </VKPanel>

                <VKPanel id="UserSettings2" v-if="zis">
                    <v-k-panel-header>Взлом Жопы</v-k-panel-header>
                    <v-text-field v-model="Password" type="password" class="center-pic" style="width: 50%" label="Пароль"/>
                    <v-btn @click="checkAdminPassword" style="opacity: 0.8" color="success">Отправить</v-btn>
                    <v-alert border="left"
                             colored-border
                             :color="Password_ === 'success' ? 'green accent-3' : 'deep-purple accent-4'"
                             class="center-pic"
                             style="width: 50%;margin-top: 25px"
                             elevation="2"
                             icon="mdi-bacteria-outline"
                             v-if="!isNull(Password_)">
                        {{Password_text}}
                    </v-alert>
                </VKPanel>

            </VKView>

            <v-dialog v-model="dialog" fullscreen>
                <v-card>
                    <v-toolbar style="border-bottom-right-radius: 25px;border-bottom-left-radius: 25px;">

                        <v-card-actions>
                            <v-btn depressed fab @click="dialog = false"
                                   small left
                                   :color="this.$vuetify.theme.dark ? 'white' : '#c0c0c0'"
                                   style="opacity: 0.8">
                                <v-icon :color="this.$vuetify.theme.dark ? 'black' : 'white'">mdi-arrow-left</v-icon>
                            </v-btn>
                        </v-card-actions>

                        <v-toolbar-title>Фотография</v-toolbar-title>

                    </v-toolbar>
                    <v-container>
                        <v-img :src="photos.length > 1 ? getImgUrl(image) : image"
                               contain
                               style="border-radius: 5px; width: 85%;" class="center-pic"/>
                        <br>
                        <p>{{text_modal}}</p>
                    </v-container>
                </v-card>
            </v-dialog>
<!--            <v-snackbar-->
<!--                    v-model="alert_upload"-->
<!--                    bottom-->
<!--                    :timeout="3000">-->
<!--                {{ alert_upload_text }}-->
<!--                <v-btn text-->
<!--                       color="pink"-->
<!--                       @click="alert_upload = false">-->
<!--                    Закрыть-->
<!--                </v-btn>-->
<!--            </v-snackbar>-->
        </v-app>
    </div>

</template>
<script>
    import axios from 'axios';
    import vk_connect from "@vkontakte/vk-connect";


    const url_flask = 'http://127.0.0.1:5000';

export const http = axios.create({
    baseURL: `${url_flask}/api`
});

Date.prototype.getUnixTimestamp = function() {

    return Math.round(this.getTime() / 1000);

};

    // const app_id = 7194877;
const hash = window.location.search;
export default {
    name: 'app',
    data() {
      return {
          activePanel: 'Main',
          datePanel: new Date().getTime(),
          activeBtn: 0,
          token: '',
          name: '',
          current_id: null,
          userAvatar: '',
          image: 'photo-1.jpg',
          text_modal: 1,
          photos: [],
          dialog: false,
          loading_service: false,
          Password: '',
          Password_: null,
          Password_text: null,
          count: 20,
          getRandomPhotos: true,
          comment: null,
          zis: '',
      };
    },
    methods:{
        isNull(val) {
            return val === null;
        },
        getImgUrl(pic) {
            return url_flask + pic;
        },
        setModalInfo(p, t) {
            this.image = p;
            this.text_modal = t;
        },
        setLikeOnPhoto_null(photo, id){
             this.photos[id].count_like += 1;
             this.photos[id].like = true;
             this.setLikeDisApi(photo.id, 'like')
        },
        setDisOnPhoto_null(photo, id){
            this.photos[id].count_dis += 1;
            this.photos[id].like = false;
            this.setLikeDisApi(photo.id, 'dis')
        },
        SetNightMode(){
            this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
        },
        SetActivePanel(p){
            if (this.activePanel === p){return}
            let d = new Date();
            if (d.getTime() > this.datePanel){
                this.datePanel = d.getTime() + 1000;
                this.activePanel = p;
                // eslint-disable-next-line no-console
                console.log(p)
            }
        },
        getAllPhotos() {
                http.get(`/getPhoto${hash}${hash ? '&' : '?'}count=${this.count}`).then((res) => {
                    this.photos = res.data;
                }).catch((err) => {
                    // eslint-disable-next-line no-console
                    console.log(err);
                    this.photos = [{
                        url: require('../assets/404-page-not-found.png'),
                        id: null,
                        like: true,
                        count_like: -1,
                        count_dis: -1,
                        uuid: 123,
                        text: 'Если ты это видишь, значит возникли технические шоколадки'
                    }];
                });

        },
        getUserPhoto() {
            this.dialog_profile = true;
            http.get(`/getUserPhoto${hash}`).then((res) => {
                this.profile_photos = res.data['photos'];
            }).catch(() => {
                this.profile_photos = [];
            })
        },
        setLikeDisApi(id, where){
            http.get(`/setPhotoLikeDis${hash}${hash ? '&' : '?'}id=${id}&${where}=1`)
                .then(() => {

                })
                .catch(() => {

                });
        },
        getUser(){
            http.get(`/getUser${hash}${hash ? '&' : '?'}`)
                .then((res) => {
                    res = res.data;
                    this.count = res.count_photo;
                    this.getRandomPhotos = res.random;
                    this.comment = res.comment;
                    this.getAllPhotos();
                    this.zis = res.zis
                })
                .catch(() => {

                });
        },
        checkAdminPassword(){
            http.get(`/checkAdminPassword${hash}${hash ? '&' : '?'}pass=${this.Password}`)
                .then((res) => {
                    this.Password_ = res.data.status;
                    this.Password_text = res.data.text
                })
                .catch(() => {

                });
        },
        abbreviateNumber(value) {
            let newValue = value;
            if (value >= 1000) {
                let suffixes = ["", "k", "m", "b","t"];
                let suffixNum = Math.floor( (""+value).length/3 );
                let shortValue = '';
                for (var precision = 2; precision >= 1; precision--) {
                    shortValue = parseFloat( (suffixNum != 0 ? (value / Math.pow(1000,suffixNum) ) : value).toPrecision(precision));
                    var dotLessShortValue = (shortValue + '').replace(/[^a-zA-Z 0-9]+/g,'');
                    if (dotLessShortValue.length <= 2) { break; }
                }
                if (shortValue % 1 != 0)  shortValue = shortValue.toFixed(1);
                newValue = shortValue+suffixes[suffixNum];
            }
            return newValue;
        },
        triggerFileUploads(){
            this.$refs.fileInput.click()
        },
        async onFileChange(event){
            let file = event.target.files[0];
            let data = new FormData();
            data.append("photo", file);

            try {
                await  fetch(`${url_flask}/api/UploadImage${hash}`, {method: "POST", body: data});
                this.alert_upload_text = 'Фотография загруженаю.';
            } catch {
                this.alert_upload_text = 'Ошибка при загрузке.';
            } finally {
                this.alert_upload = true;
            }

        }
    },

    created() {
        this.getUser();
    },
    mounted(){
        document.body.setAttribute("scheme", "client_light");
        vk_connect.sendPromise("VKWebAppGetUserInfo", {}).then(r => {
            this.name = r.first_name + ' ' + r.last_name;
            this.current_id = r.id;
            this.userAvatar = r.photo_max_orig;

        })
        // vk_connect.sendPromise("VKWebAppGetAuthToken", { app_id, scope: "friends" }).then(r => r.access_token).then(access_token => {this.token = access_token});

    },
}


</script>

<style>
  .center-pic{
      display:block;
      margin:auto;
  }

  body::-webkit-scrollbar {
      display: none;
      width: 0;
  }

</style>